.Menu {
	height: 50vh;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	background-color: #151f21;
	overflow: hidden;
}

.MenuButton {
	width: 50%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	border: none;
	background: transparent;
	font-family: inherit;
	font-size: 2rem;
	color: #feffef;
}

.MenuButton:hover {
	cursor: pointer;
}

.MenuButton:active,
.MenuButton:focus,
.MenuButton:hover,
.MenuButton:visited {
	outline: none !important;
}


