.Host>div>.code {
	text-align: center;
	min-height: 4rem;
	height: 10vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
}

.Host>div>.start {
	height: 8vh;
	border: none;
	width: 100%;
	text-align: center;
	background: #151f21;
	color: #feffef;
	font-family: inherit;
	font-size: 2rem;
	outline: none !important;
	text-transform: uppercase;
	cursor: pointer;
}

.prep {
	height: 90vh;
	width: 100%;
	background: #151f21;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.timer {
	width: 100%;
	height: 10vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
}

.timer>h2 {
	color: #feffef;
}

.is-ready {
	height: 10vh;
	width: 100%;
	color: #feffef;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.is-ready>h2 {
	color: #feffef;
}


