.Title {
	width: 100%;
	height: 50vh;
	min-height: 4rem;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}

.Title>h1 {
	-ms-flex-positive: 0;
	    flex-grow: 0;
	margin: 0 2rem;
}

.Title>h1>a {
	text-decoration: none;
}

.Title>.rainbow {
	-ms-flex-positive: 1;
	    flex-grow: 1;
}

.Title>.rainbow>div {
	width: 100%;
	height: .5rem;
}


