.Header {
	width: 100%;
	height: 10vh;
	min-height: 2rem;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}

.Header>h1 {
	-ms-flex-positive: 0;
	    flex-grow: 0;
	margin: 0 2rem;
	font-size: 2rem;
}

.Header>h1>a {
	text-decoration: none;
}

.Header>.rainbow {
	-ms-flex-positive: 1;
	    flex-grow: 1;
}

.Header>.rainbow>div {
	width: 100%;
	height: .25rem;
}


