.Connector {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.Connector>input {
	width: 100%;
	text-align: center;
	background: transparent;
	border: none;
	font-size: 2rem;
	font-family: inherit;
	min-height: 2rem;
	height: 15vh;
	outline: none;
}

.Connector>input::-webkit-input-placeholder {
	color: #151f21;
}

.Connector>input:-ms-input-placeholder {
	color: #151f21;
}

.Connector>input::-ms-input-placeholder {
	color: #151f21;
}

.Connector>input::placeholder {
	color: #151f21;
}

.room-input {
	text-transform: uppercase;
}


